import React from "react";
import "./HelpPage.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHistory } from "@fortawesome/free-solid-svg-icons";

function ImportHelpSection() {
  return (
    <div className="content">
      <h4>Nommer les mesures</h4>

      <p>
        Lors de l'importation de mesures, il est possible d'indiquer un nom afin
        d'identifier rapidement ces dernires.
        <br /> Le nom est facultatif et n'a pas besoin d'tre unique.
      </p>

      <h4>Selectionner le fichier de mesures</h4>

      <p>
        L'importation des mesures n'accepte que le format CSV. Les enttes
        suivantes doivent tre prsentes dans le fichier :
      </p>

      <pre>
        Reading #,Method Name,Elevation,Latitude,Longitude,Date,Time, Units,[XX
        Concentration, XX Error1s]?,Divers,Oprateur,Projet
      </pre>

      <ul>
        <li>
          "XX Concentration" correspond  la concentration pour l'lment XX
          (exemple: "<em>Ag Concentration</em>"
        </li>
        <li>
          "XX Error1s" correspond  la marge d'erreur pour l'lment XX
          (exemple: "<em>Ag Error1s</em>"
        </li>
        <li>
          Les mesures sont groupes en fonction de la valeur dans le colonne
          "Divers"
        </li>
        <li>
          L'importation reste fonctionnel si la premire ligne du fichier
          contient un indicateur de sparateur (ex. "<em>sep=,</em>")
        </li>
      </ul>

      <h4>Historique des mesures importes</h4>

      <p>
        Il n'est pas necessaire d'importer un fichier  chaque fois pour le
        visualiser. En effet, un systme d'historique est disponible dans
        l'application via le bouton{" "}
        <span className="icon is-small">
          <FontAwesomeIcon icon={faHistory} />
        </span>{" "}
        prsent dans le menu de navigation.
      </p>
      <p>
        De plus, les mesures ignores via la section "Donnes brutes" sont
        gardes en mmoire et restaures lors de l'utilisation de l'historique.
      </p>
    </div>
  );
}

export default ImportHelpSection;
